@mixin font($weight, $size, $line-height, $color) {
  font-family: Roboto, sans-serif;
  font-weight: $weight;
  font-style: normal;
  font-size: $size;
  line-height: $line-height;
  color: $color;
}

@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin hover-transition {
  transition-property: all;
  transition-duration: 300ms;
}
