html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__moyo-link {
  cursor: pointer;
  height: 40px;
  margin: 0 5px 0 0;
}

.header__list {
  gap: 20px;
  margin: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.header__item {
  position: relative;
}

.header__link {
  text-wrap: nowrap;
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  line-height: 60px;
  transition-property: all;
  transition-duration: .3s;
  display: inline-block;
}

.header__link.is-active {
  color: #00acdc;
}

.header__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: auto;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__link:hover {
  cursor: pointer;
  color: #00acdc;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition-property: all;
  transition-duration: .3s;
  display: flex;
}

.card__image-link {
  align-self: center;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 40px;
}

.card__image {
  width: 160px;
  height: 134px;
  transition-property: all;
  transition-duration: .3s;
  display: block;
}

.card__image:hover {
  cursor: pointer;
  outline: 3px solid #00acdc;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  transition-property: all;
  transition-duration: .3s;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__amount-of-reviews {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  transition-property: all;
  transition-duration: .3s;
  display: flex;
}

.card__buy-link:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: translateY(-20%);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__title:hover {
  cursor: pointer;
  color: #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

